// pdf
import html2Canvas from 'html2canvas';
import jsPDF from 'jspdf';

export const htmlToPdf = {
  getPdf: (dom, title, loading) => {
    // loading = true;
    let options= {
      taintTest: false,
      allowTaint: false,
      logging: false,
      useCORS: true,
      dpi: window.devicePixelRatio * 4, //DPI 
      scale: 4, //
    }
    html2Canvas(dom,options).then((canvas) => {
      const pdf = new jsPDF('p', 'mm', 'a4'); //A4
      let ctx = canvas.getContext('2d'),
        a4w = 190,
        a4h = 272, //A4210mm x 297mm10mm190x277
        imgHeight = Math.floor((a4h * canvas.width) / a4w), //A4
        renderedHeight = 0;

      while (renderedHeight < canvas.height) {
        const page = document.createElement('canvas');
        page.width = canvas.width;
        page.height = Math.min(imgHeight, canvas.height - renderedHeight); //

        //getImageDatacanvas
        page.getContext('2d')
          .putImageData(
            ctx.getImageData(
              0,
              renderedHeight,
              canvas.width,
              Math.min(imgHeight, canvas.height - renderedHeight),
            ),
            0,
            0,
          );
        pdf.addImage(
          page.toDataURL('image/jpeg', 1.0),
          'JPEG',
          10,
          10,
          a4w,
          Math.min(a4h, (a4w * page.height) / page.width),
        ); //10mm

        renderedHeight += imgHeight;
        if (renderedHeight < canvas.height) {
          pdf.addPage(); //
        }
        // delete page;
      }
      //
      pdf.save(title + '.pdf');
      // loading = false;
    });
  },
};